<template>
  <div class="body">
    <div class="header">
      <div class="title">
        <span class="h3">zuxian</span>
        <h1>CODIV-19（corona-virus）</h1>
        <h5>Real Time Updates With Baidu Bigdata</h5>
      </div>
      <!--<img   class="img" src="../assets/1.jpg" alt="corona-virus">-->
    </div>
    <div class="main">
      <h2 class="title">statistics<span class="updateTime">Last updated：{{updateTime}}</span></h2>
      <div class="statistics">
          <div class="numbersum">
            <div class="confirm">{{data.confirm}}</div>
            <div class="desc">Confirmed</div>
          </div>
          <div class="numbersum">
            <div class="suspect">{{data.suspect}}</div>
            <div class="desc">Suspect</div>
          </div>
          <div class="numbersum">
            <div class="heal">{{data.heal}}</div>
            <div class="desc">Recovered</div>
          </div>
          <div class="numbersum">
            <div class="dead">{{data.dead}}</div>
            <div class="desc">Deaths</div>
          </div>
      </div>
      <h2 class="title">Case Map</h2>
      <virus-map></virus-map>
      <h2 class="title">Statistics of provinces in China</h2>
      <table-tree></table-tree>
    </div>
    <div class="header">
      <div class="title">
        <h3> Overcome the epidemic, pass the positive energy!</h3>
        <!--<h5>项目源代码：<a href="https://github.com/" class="github">https://github.com/</a></h5>-->
      </div>
    </div>
  </div>
</template>

<script>
import VirusMap from './VirusMap'
import TableTree from './TableTree'
import originData from '../../static/originData.json'

export default {
  name: 'Index',
  components: {
    VirusMap,
    TableTree
  },
  data () {
    return {
      data: {},
      updateTime: ''
    }
  },
  methods: {
    initData () {
      this.data = originData.chinaTotal;
      this.updateTime = originData.lastUpdateTime
    }
  },
  mounted () {
    this.initData()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
.header {
  width: 100%;
  height: 200px;
  /*background-color: skyblue;*/
  background: url(../assets/3.jpg) no-repeat;
  background-size:100% 100%;
  display: flex;
  justify-content: space-between;
  color: white;
  .title {
    margin-left: 135px;
    margin-top: 35px;
    .github {
      text-decoration: none;
      color: #fff;
    }
  }
  /*.img {*/
    /*width: auto;*/
    /*height: auto;*/
    /*width: 100%;*/
    /*height: 100%;*/

    /*max-width: 100%;*/
    /*max-height: 100%;*/
    /*z-index: 1;*/
  /*}*/
  .h3 {
    /*background-color: rgba(255, 255, 255, 0.3);*/
    padding: 0 10px;
  }
}
.main {
  width: 80%;
  margin: 20px auto;
  .title {
    text-decoration: goldenrod;
    &::before {
      content: '';
      width: 20px;
      height: 100%;
      background-color: goldenrod;
    }
    .updateTime {
      font-size: 14px;
      font-weight: 500;
      float: right;
    }
  }
  .statistics {
    display: flex;
    justify-content: space-between;
    text-align: center;
    background-color: #f5f6f7;
    padding: 10px 40px;
    border-radius: 10px;
    .numbersum {
      /*color: #22aeb4;*/
      .suspect,.confirm,.heal, .dead {
        font-size: 20px;
        font-weight: 900;
      }
      .confirm {color: red;  }
      .suspect { color: sandybrown;  }
      .heal{color: forestgreen;}
      .dead{color: #111111; }


      .desc{ font-size: 15px; }

    }






  }
}
</style>



